"use client"

import type React from "react"

import { useState, useEffect } from "react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { useToast } from "@/hooks/use-toast"
import { useAuth } from "@/hooks/use-auth"
import { AuthService } from "@/lib/auth"
import { Loader2, Mail } from "lucide-react"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"

export default function LoginPage() {
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [loading, setLoading] = useState(false)
  const [isRedirecting, setIsRedirecting] = useState(false)
  const [showEmailConfirmationAlert, setShowEmailConfirmationAlert] = useState(false)
  const [resendingEmail, setResendingEmail] = useState(false)
  const { login, user, loading: authLoading } = useAuth()
  const { toast } = useToast()
  const router = useRouter()

  // Redirect if already logged in (but only if not already redirecting)
  useEffect(() => {
    if (!authLoading && user && !isRedirecting && !loading) {
      // User is already logged in, redirect to appropriate page
      // Use window.location.href to ensure full page reload and cookie sync
      if (user.role === "admin") {
        window.location.href = "/admin"
      } else if (!user.approved) {
        window.location.href = "/pending-approval"
      } else {
        window.location.href = "/dashboard"
      }
    }
  }, [user, authLoading, isRedirecting, loading])

  const handleResendConfirmation = async () => {
    if (!email) {
      toast({
        title: "Email required",
        description: "Please enter your email address first.",
        variant: "destructive",
      })
      return
    }

    setResendingEmail(true)
    try {
      await AuthService.resendConfirmationEmail(email)
      toast({
        title: "Confirmation email sent",
        description: "Please check your inbox for the confirmation link.",
      })
      setShowEmailConfirmationAlert(false)
    } catch (error) {
      toast({
        title: "Failed to resend email",
        description: error instanceof Error ? error.message : "Something went wrong. Please try again.",
        variant: "destructive",
      })
    } finally {
      setResendingEmail(false)
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setShowEmailConfirmationAlert(false) // Hide alert on new login attempt

    let timeoutId: NodeJS.Timeout | null = null

    // Add timeout protection
    const loginPromise = (async () => {
      try {
        console.log('Attempting login for:', email)
        const user = await login(email, password)
        
        console.log('Login successful, user:', user)
        console.log('User role:', user.role)
        console.log('User approved:', user.approved)
        
        toast({
          title: "Welcome back!",
          description: "You have successfully logged in.",
        })

        // Sync session to cookies immediately after login
        // This ensures server-side can read the session
        console.log('Syncing session to cookies...')
        try {
          if (typeof window !== 'undefined') {
            const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL || ''
            const projectRef = supabaseUrl.split('//')[1]?.split('.')[0] || ''
            const storageKey = `sb-${projectRef}-auth-token`
            const stored = localStorage.getItem(storageKey)
            
            if (stored) {
              try {
                const parsed = JSON.parse(stored)
                if (parsed.access_token && parsed.refresh_token) {
                  // Sync to cookies via API route (more reliable than server action)
                  const syncResponse = await fetch('/api/auth/sync-cookies', {
                    method: 'POST',
                    headers: {
                      'Content-Type': 'application/json',
                    },
                    body: JSON.stringify({
                      accessToken: parsed.access_token,
                      refreshToken: parsed.refresh_token,
                    }),
                  })

                  const result = await syncResponse.json()
                  if (result.success) {
                    console.log('✓ Session synced to cookies successfully')
                    // Wait a bit more to ensure cookies are fully set
                    await new Promise(resolve => setTimeout(resolve, 300))
                  } else {
                    console.warn('⚠ Failed to sync session to cookies:', result.error)
                    // Even if sync failed, wait a bit before redirect
                    await new Promise(resolve => setTimeout(resolve, 300))
                  }
                } else {
                  // No tokens found, wait anyway
                  await new Promise(resolve => setTimeout(resolve, 300))
                }
              } catch (e) {
                console.warn('Failed to parse session for cookie sync:', e)
                await new Promise(resolve => setTimeout(resolve, 300))
              }
            } else {
              console.warn('⚠ Session not found in localStorage for cookie sync')
              // Wait before redirect even if no session in localStorage
              await new Promise(resolve => setTimeout(resolve, 300))
            }
          }
        } catch (syncError) {
          console.error('Error syncing session to cookies:', syncError)
          // Continue anyway - middleware might still work
          await new Promise(resolve => setTimeout(resolve, 300))
        }

        // Mark as redirecting to prevent useEffect from interfering
        setIsRedirecting(true)

        // Additional wait to ensure everything is ready
        await new Promise(resolve => setTimeout(resolve, 200))

        // Redirect based on role and approval status
        // Use window.location.href for full page reload to ensure middleware reads cookies
        if (user.role === "admin") {
          console.log('Redirecting admin to /admin')
          window.location.href = "/admin"
        } else if (!user.approved) {
          console.log('User not approved, redirecting to pending approval')
          window.location.href = "/pending-approval"
        } else {
          console.log('Redirecting user to /dashboard')
          window.location.href = "/dashboard"
        }
        
        // Prevent further execution (though window.location.href should handle this)
        return
      } catch (error) {
        console.error('Login error:', error)
        let errorMessage = "Invalid credentials. Please try again."
        
        if (error instanceof Error) {
          // Check for email not confirmed error
          if (error.message === 'Email not confirmed' || (error as any).code === 'EMAIL_NOT_CONFIRMED') {
            setShowEmailConfirmationAlert(true)
            errorMessage = "Please confirm your email address before logging in. Check your inbox for a confirmation email."
          } else {
            errorMessage = error.message
            
            // Provide more helpful error messages
            if (error.message.includes('Supabase is not configured')) {
              errorMessage = "Configuration error: Supabase environment variables are missing. Please check your .env file."
            } else if (error.message.includes('timed out')) {
              errorMessage = "Connection timeout. Please check your internet connection and try again."
            } else if (error.message.includes('User profile not found')) {
              errorMessage = "User account not found. Please contact support or try registering a new account."
            } else if (error.message.includes('pending admin approval')) {
              errorMessage = "Your account is pending admin approval. You will be notified once approved."
            } else if (error.message.includes('Invalid email or password')) {
              errorMessage = "Invalid email or password. Please check your credentials and try again."
            } else if (error.message.includes('RLS policy')) {
              errorMessage = "Access denied. Please contact support if this issue persists."
            }
          }
        }
        
        toast({
          title: "Login failed",
          description: errorMessage,
          variant: "destructive",
        })
        throw error // Re-throw to be caught by timeout handler
      }
    })()

    // Timeout handler
    const timeoutPromise = new Promise((_, reject) => {
      timeoutId = setTimeout(() => {
        reject(new Error('Login request timed out. Please check your connection and try again.'))
      }, 30000) // 30 second timeout to match auth service
    })

    try {
      await Promise.race([loginPromise, timeoutPromise])
    } catch (error) {
      // Error already handled in loginPromise catch block
      // This catch is mainly for timeout errors
      if (error instanceof Error && error.message.includes('timed out')) {
        toast({
          title: "Login timeout",
          description: "The login request took too long. Please check your connection and try again.",
          variant: "destructive",
        })
      }
    } finally {
      if (timeoutId) {
        clearTimeout(timeoutId)
      }
      setLoading(false) // Always reset loading state
    }
  }

  // Show loading while checking auth state
  if (authLoading) {
    return (
      <div className="flex min-h-[calc(100vh-80px)] items-center justify-center">
        <div className="text-center">
          <Loader2 className="mb-4 h-8 w-8 animate-spin mx-auto" />
          <p className="text-muted-foreground">Loading...</p>
        </div>
      </div>
    )
  }

  // Don't show login form if user is already logged in (redirecting)
  if (user) {
    return (
      <div className="flex min-h-[calc(100vh-80px)] items-center justify-center">
        <div className="text-center">
          <Loader2 className="mb-4 h-8 w-8 animate-spin mx-auto" />
          <p className="text-muted-foreground">Redirecting...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="flex min-h-[calc(100vh-80px)] items-center justify-center bg-accent/30 px-4 py-12">
      <Card className="w-full max-w-md">
        <CardHeader className="space-y-1">
          <CardTitle className="text-2xl font-bold">Sign In</CardTitle>
          <CardDescription>Enter your credentials to access your account</CardDescription>
        </CardHeader>
        <CardContent>
          {showEmailConfirmationAlert && (
            <Alert className="mb-4 border-amber-200 bg-amber-50 dark:border-amber-800 dark:bg-amber-950">
              <Mail className="h-4 w-4 text-amber-600 dark:text-amber-400" />
              <AlertTitle className="text-amber-800 dark:text-amber-200">Email Not Confirmed</AlertTitle>
              <AlertDescription className="text-amber-700 dark:text-amber-300">
                <p className="mb-3">
                  Please confirm your email address before logging in. Check your inbox (and spam folder) for a confirmation email.
                </p>
                <Button
                  type="button"
                  variant="outline"
                  size="sm"
                  onClick={handleResendConfirmation}
                  disabled={resendingEmail}
                  className="border-amber-300 text-amber-800 hover:bg-amber-100 dark:border-amber-700 dark:text-amber-200 dark:hover:bg-amber-900"
                >
                  {resendingEmail ? (
                    <>
                      <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                      Sending...
                    </>
                  ) : (
                    <>
                      <Mail className="mr-2 h-4 w-4" />
                      Resend Confirmation Email
                    </>
                  )}
                </Button>
              </AlertDescription>
            </Alert>
          )}
          
          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="email">Email Address</Label>
              <Input
                id="email"
                type="email"
                placeholder="you@company.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                disabled={loading}
              />
            </div>

            <div className="space-y-2">
              <div className="flex items-center justify-between">
                <Label htmlFor="password">Password</Label>
                <Link href="/forgot-password" className="text-sm text-primary hover:underline">
                  Forgot password?
                </Link>
              </div>
              <Input
                id="password"
                type="password"
                placeholder="••••••••"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
                disabled={loading}
              />
            </div>

            <Button type="submit" className="w-full" disabled={loading}>
              {loading ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Signing in...
                </>
              ) : (
                "Sign In"
              )}
            </Button>
          </form>

          <div className="mt-6 space-y-4">
            <div className="relative">
              <div className="absolute inset-0 flex items-center">
                <span className="w-full border-t border-border" />
              </div>
              <div className="relative flex justify-center text-xs uppercase">
                <span className="bg-card px-2 text-muted-foreground">Demo Credentials</span>
              </div>
            </div>

            <div className="space-y-2 rounded-lg bg-muted/50 p-4 text-xs">
              <p className="font-medium text-foreground">SMME User:</p>
              <p className="text-muted-foreground">Email: user@smme.com</p>
              <p className="text-muted-foreground">Password: demo123</p>

              <p className="mt-3 font-medium text-foreground">Admin:</p>
              <p className="text-muted-foreground">Email: admin@elidz.com</p>
              <p className="text-muted-foreground">Password: demo123</p>
            </div>
          </div>

          <p className="mt-6 text-center text-sm text-muted-foreground">
            Don't have an account?{" "}
            <Link href="/register" className="font-medium text-primary hover:underline">
              Sign up
            </Link>
          </p>
        </CardContent>
      </Card>
    </div>
  )
}
